'use client';
import React, { useState } from 'react';
import { VentaServicio, Cliente, DetalleServicio } from '../../../entities/venta';
import { useVentas } from '../../../hooks/useVentas';
import { formatCurrency } from '../../../lib/logistica/formatters';

const clientesDummy: Cliente[] = [
  { idCliente: 'CL-001', nombre: 'Consultora ABC', telefono: '999999999', correo: 'abc@mail.com' },
  { idCliente: 'CL-002', nombre: 'Hotel Pacifico', telefono: '988888888', correo: 'hotel@pacifico.pe' }
];

const serviciosDisponibles = [
  { idServicio: 'SVC-001', nombre: 'Instalación Red', costo: 200 },
  { idServicio: 'SVC-002', nombre: 'Soporte Técnico', costo: 100 }
];

const VentaForm = () => {
  const { registrarVenta } = useVentas();
  const [cliente, setCliente] = useState<Cliente | null>(null);
  const [detalles, setDetalles] = useState<DetalleServicio[]>([]);
  const [descuento, setDescuento] = useState<number>(0);
  const [error, setError] = useState<string | null>(null);

  const agregarServicio = (id: string) => {
    const servicio = serviciosDisponibles.find(s => s.idServicio === id);
    if (servicio && !detalles.some(d => d.idServicio === id)) {
      setDetalles(prev => [...prev, {
        idDetalleServicio: `DET-${Date.now()}`,
        idServicio: servicio.idServicio,
        nombreServicio: servicio.nombre,
        costoTotal: servicio.costo,
      }]);
    }
  };

  const limpiarFormulario = () => {
    setCliente(null);
    setDetalles([]);
    setDescuento(0);
    setError(null);
  };

  const total = detalles.reduce((acc, s) => acc + s.costoTotal, 0) - descuento;

  const handleSubmit = () => {
    if (!cliente || detalles.length === 0) {
      setError('Debes seleccionar un cliente y al menos un servicio.');
      return;
    }

    const nuevaVenta: VentaServicio = {
      idVentaServicio: `VENTA-${Date.now()}`,
      cliente,
      detalles,
      descuento,
      costoTotal: total,
      fechaRegistro: new Date().toISOString()
    };

    registrarVenta(nuevaVenta);
    limpiarFormulario();
    alert('✅ Venta registrada con éxito');
  };

  return (
    <div className="space-y-4">
      {error && <p className="text-red-600 text-sm">{error}</p>}

      <select className="border p-2 rounded w-full" value={cliente?.idCliente || ''} onChange={(e) => {
        const sel = clientesDummy.find(c => c.idCliente === e.target.value);
        if (sel) setCliente(sel);
      }}>
        <option value="">Seleccionar cliente</option>
        {clientesDummy.map(c => <option key={c.idCliente} value={c.idCliente}>{c.nombre}</option>)}
      </select>

      <select className="border p-2 rounded w-full" onChange={(e) => {
        if (e.target.value !== '') agregarServicio(e.target.value);
        e.target.value = '';
      }}>
        <option value="">Agregar servicio</option>
        {serviciosDisponibles.map(s => (
          <option key={s.idServicio} value={s.idServicio}>{s.nombre} - {formatCurrency(s.costo)}</option>
        ))}
      </select>

      <ul className="list-disc pl-5 text-sm text-gray-700">
        {detalles.map(d => (
          <li key={d.idDetalleServicio}>{d.nombreServicio} – {formatCurrency(d.costoTotal)}</li>
        ))}
      </ul>

      <input
        type="number"
        className="border p-2 rounded w-full"
        placeholder="Descuento (S/.)"
        value={descuento}
        onChange={(e) => setDescuento(Number(e.target.value))}
      />

      <div className="flex justify-between text-sm text-gray-700">
        <p><strong>Total:</strong> {formatCurrency(total)}</p>
        <button onClick={limpiarFormulario} className="text-blue-600 underline text-sm">Limpiar</button>
      </div>

      <button
        onClick={handleSubmit}
        disabled={!cliente || detalles.length === 0}
        className="bg-blue-600 text-white px-4 py-2 rounded disabled:bg-gray-400"
      >
        Registrar Venta
      </button>
    </div>
  );
};

export default VentaForm;
